html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
    user-select: none;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}

@tailwind base;
@tailwind components;
@tailwind utilities;

@layer components {
    .pdfembed {
        width: 95%;
        aspect-ratio: 1/1.41;
        max-width: 550px;
        margin-top: 2px;
    }
    
    .socialinks {
        @apply mx-1 sm:mx-[0.3rem] lg:hover:text-blue-400 lg:hover:border-blue-400 transition-all border border-gray-600 rounded-full p-[10px];
    }

    .childrenDiv{
        @apply rounded-full bg-gray-50 p-2 w-[85px] aspect-square flex justify-center bg-opacity-70;
    }

    .childrenPara{
        @apply text-center font-outfit mt-1 font-medium;
    }
}

::-webkit-scrollbar {
    display: none;
  }
  
  /* Hide scrollbar for IE, Edge and Firefox */
  html {
    -ms-overflow-style: none;  /* IE and Edge */
    scrollbar-width: none;  /* Firefox */
  }